






import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import * as Qrcode from 'qrcode';

@Component({
})
export default class VQrcode extends Vue {
    @Prop()
    public code!: string;
    @Prop({default : 'canvas'})
    public tag!: string;
    @Prop({type: Object, default: () => ({errorCorrectionLevel: 'H'})})
    public options!: Qrcode.QRCodeOptions;

    public mounted() {
        return this.generate();
    }
    @Watch('$props',  {
        immediate: true,
        deep: true,
    })
    public onProps() {
        this.generate();
    }

    public generate() {
        const { options, tag } = this;
        const value = this.code.toString();

        if (tag === 'canvas') {
            Qrcode.toCanvas(this.$el, value, options).catch((reason) => {
                throw reason;
            });
        } else if (tag === 'img') {
            Qrcode.toDataURL(value, options).then((url) => {
                (this.$el as HTMLImageElement).src = url;
            }).catch((reason) => {
                throw reason;
            });
        } else {
            Qrcode.toString(value, options).then((str) => {
                this.$el.innerHTML = str;
            }).catch((reason) => {
                throw reason;
            });
        }
    }
}
